.popup {
	font-family: 'Inter', 'Arial', sans-serif;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	opacity: 0;
	visibility: hidden;
	transition: all 0.8s ease 0s;
	display: flex;
	justify-content: center;
	align-items: center;
}